
import { defineComponent } from 'vue';
import { BackupType, PaginationType } from '@/types';

export default defineComponent({
  name: 'page-ratings',
  data() {
    return {
      // loading data
      isLoading: false,
      // backups
      backups: [] as BackupType[],
      // Pagination
      pagination: {
        page: 1,
        itemsCount: 0,
        maxRecordsPerPage: 0,
        pagesCount: 0,
      } as PaginationType,
    };
  },

  mounted() {
    this.init();
  },

  methods: {
    // Init
    async init() {
      await this.getBackups();
    },

    // Get backups
    async getBackups() {
      if (this.isLoading) return;
      this.isLoading = true;

      try {
        let backup = await this.$api['backups'].getBackups({ page: this.pagination.page });
        let { items, page, itemsCount, maxRecordsPerPage, pagesCount } = backup;
        this.backups = items;
        this.pagination = {
          page,
          itemsCount,
          maxRecordsPerPage,
          pagesCount,
        };
      } catch (errors: any) {
        this.$utils.showMessageError({ message: errors.server, errors });
      } finally {
        this.isLoading = false;
      }
    },

    // Get backups for page
    async changePage(page: number) {
      this.pagination.page = page;
      await this.getBackups();
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
      this.isLoading = false;
    },
  },
});
